import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';


export class Post {
  postid: number;
  created: Date;
  modified: Date;
  title: string;
  body: string;
}

function parseJWT(token) 
{
    let base64Url = token.split('.')[1];
    let base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    return JSON.parse(atob(base64));
}

function getUsername() {
  return parseJWT(document.cookie).usr;
}


@Injectable({
  providedIn: 'root'
})
export class BlogService {

  private posts: Post[];

  constructor(private http: HttpClient, private router: Router) {
    this.fetchPosts();
  }

  /* Populate the posts property by retrieving all blog posts of the current user */
  fetchPosts(): void {
  	this.posts = [];
    let username = getUsername();
    let url = "/api/" + username;
  	/*this.http.get<Post[]>(url)
      .subscribe(posts => this.posts = posts);*/
    let xmlHttp = new XMLHttpRequest();
    xmlHttp.open('GET', url, false);  // `false` makes the request synchronous
    xmlHttp.send(null);
    if (xmlHttp.status === 200) {
      let res = JSON.parse(xmlHttp.responseText);
      for(let doc of res) {
        let post = {
          postid: doc.postid,
          created: doc.created,
          modified: doc.modified,
          title: doc.title,
          body: doc.body
        };
        this.posts.push(post);
      }
    }
  }

  /* GET all posts of user */
  getPosts(): Post[] {
  	return this.posts;
  }

  /* GET the post with postid=id from posts*/
  getPost(id: number): Post {
  	for(let p of this.posts) {
  		if(p.postid == id) return p;
  	}
  	return null;
  }

  /* POST: add a new post  */
  newPost(): Post {
  	let postid = 1;
    if(this.posts) {
  		//posts should always be sorted with ascending postids so the max will be at the end
  		postid = this.posts[this.posts.length-1].postid + 1;
  	}
  	let username = getUsername();
  	let url = "/api/" + username + "/" + postid;
  	let created = new Date();
  	let post: Post = {'postid': postid, 'created': created, 'modified': created, 'title': "", 'body': ""};
  	this.posts.push(post);

  	this.http.post<Post>(url, post).subscribe(
      data => console.log(),
  		error => {
  			if(error.status != 201 && error.status != 200) {
  				alert("Error creating a new post at the server!");
  				this.posts.splice(this.posts.length-1,1); //delete last post added
  				this.router.navigate(['/']);
  			}
  		}
  	);
    return post;
  }

  /* PUT: update the post*/
  updatePost(post: Post): void {
  	for(let i in this.posts) {
  		if(this.posts[i].postid == post.postid) {
  			this.posts[i].title = post.title;
  			this.posts[i].body = post.body;
  			this.posts[i].modified = new Date();
        let username = getUsername();
        let url = '/api/' + username + '/' + post.postid;

        this.http.put(url, post).subscribe(
          data => console.log(),
          error => {
            if(error.status != 200) {
              alert("Error updating the post at the server!");
              this.router.navigate(['/edit/',post.postid]);
            }
          }
        );

      }
    }

  		
  }

  /* DELETE: delete the post whose postid is the same as the passed in value*/
  deletePost(postid: number): void {
    let n = this.posts.length;
  	for(let i=0; i<n; i++) {
  		if(this.posts[i] && this.posts[i].postid == postid) {
  			this.posts.splice(i,1);
        let username = getUsername();
    		let url = '/api/' + username + '/' + postid;

    		this.http.delete<Post>(url).subscribe(
    	    data => console.log(),
          error => {
      			if(error.status != 200 && error.status != 204) {
      				alert("Error deleting the post at the server!");
      			}
      		}
    	  );
      }
    }
  }

}
