import { Component, OnInit, HostListener } from '@angular/core';
import { Post, BlogService } from '../blog.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  post: Post;
  postForm: FormGroup;

  constructor(
    private blogService: BlogService,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
  	this.activatedRoute.paramMap.subscribe(() => this.getPost());
  }

  getPost(): void {
    const id = +this.activatedRoute.snapshot.paramMap.get('id');
  	this.post = this.blogService.getPost(id);
    if(this.post) {
      this.postForm = new FormGroup({
        title: new FormControl(this.post.title),
        body: new FormControl(this.post.body)
      });
      /*this.postForm.get('title').setValue(this.post.title);
      this.postForm.get('body').setValue(this.post.body);*/
    }
  }

  save(): void {
    this.blogService.updatePost(this.post);
    this.postForm.markAsPristine();
  }

  delete(): void {
    this.blogService.deletePost(this.post.postid);
    this.router.navigate(['/']);
  }

  preview(): void {
    if(this.postForm.dirty) this.save();
    this.router.navigate(['/preview', this.post.postid]);
  }


  @HostListener('window:beforeunload') saveEdits(): void {
    if(this.postForm.dirty) this.save();
  }

}
