import { Component, OnInit } from '@angular/core';
import { Parser, HtmlRenderer } from 'commonmark';
import { ActivatedRoute, Router } from '@angular/router';
import { Post, BlogService } from '../blog.service';

@Component({
  selector: 'app-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.css']
})
export class PreviewComponent implements OnInit {

  post: Post;
  reader: Parser;
  writer: HtmlRenderer;
  title: string;
  body: string;

  constructor(
    private blogService: BlogService,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
  	this.reader = new Parser();
	this.writer = new HtmlRenderer();
  	this.activatedRoute.paramMap.subscribe(() => this.getPost());
  }

  getPost(): void {
	  const id = +this.activatedRoute.snapshot.paramMap.get('id');
  	this.post = this.blogService.getPost(id);

  	if(this.post) {
  		this.title = this.writer.render(this.reader.parse(this.post.title));
		this.body = this.writer.render(this.reader.parse(this.post.body));
  	}
	
  }

  editPost(): void {
  	this.router.navigate(['/edit', this.post.postid]);
  }

}
